import "./App.css";
import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import visaGif from './images/visa.gif';
import amexGif from './images/amex.gif';
import mastercardGif from './images/mastercard.gif';
import discoveryGif from './images/discovery.gif';


const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
    },
  },
  textField: {
    width: 400,
    boxShadow: "5px 10px 18px #ccc"
    
  },
}));

function App() {
  const classes = useStyles();

  const [cardNum, setCardNum] = useState("");

  const [cardIcon, setCardIcon] = useState("");

  const [cardType, setCardType] = useState("Unknown");
  const [valid, setValid] = useState(true);

  const cards = [
    {
      card: "AMEX",
      sequence: "34",
      icon: amexGif,
    },
    {
      card: "AMEX",
      sequence: "37",
      icon: amexGif,
    },
    {
      card: "Discovery",
      sequence: "6011",
      icon: discoveryGif,
    },
    {
      card: "Mastercard",
      sequence: "51",
      icon: mastercardGif,
    },
    {
      card: "Mastercard",
      sequence: "54",
      icon: mastercardGif,
    },
    {
      card: "VISA",
      sequence: "4",
      icon: visaGif,
    },
  ];

  const CreditCardValidator = (e) => {
    //Store field value in state
    setCardNum(e.target.value);

    let cardVal = e.target.value;
    cardVal = cardVal.replace(/\s/g, "");
    let cardTyp;    
    let cardIco;
    let cardNumArray = [];
    let cardValid = false;

    //CardType
    cardTyp = "Unknown";
    cards.forEach(function (arrayItem) {
      if (cardVal.lastIndexOf(arrayItem.sequence, 0) === 0) {
        cardTyp = arrayItem.card;
        cardIco = arrayItem.icon;
      }
    });

    //LuhnAlgorithm
    cardNumArray = cardVal.split("");
    let sum = "";
    let evens;

    //is the card number length even or odd, this will help to double every second value
    cardNumArray.length % 2 ? (evens = true) : (evens = false);

    for (var i = 0; i < cardNumArray.length; i++) {
      //if length is odd use num % 2; duplicate or not
      if (evens) {
        i % 2
          ? (sum += String(parseInt(cardNumArray[i]) * 2))
          : (sum += String(parseInt(cardNumArray[i])));
      } else {
        i % 2
          ? (sum += String(parseInt(cardNumArray[i])))
          : (sum += String(parseInt(cardNumArray[i]) * 2));
      }
    }

    let sumArray = sum.split("");
    let divisibleVal = 0;

    for (var i = 0; i < sumArray.length; i++) {
      divisibleVal += parseInt(sumArray[i]);
    }

    cardNumArray.length >= 13 && divisibleVal % 10 === 0
      ? (cardValid = true)
      : (cardValid = false);

    setCardType(cardTyp);
    setValid(cardValid);
    setCardIcon(cardIco);
  };

  const CreditCardInput = (props) => {
    return (
      <div>
        <form
          className={classes.root}
          noValidate
          onChange={CreditCardValidator}
        >
          <TextField
            id="creditCardNumber"
            className={classes.textField}
            variant="outlined"
            color="primary"
            placeholder={"0000 0000 0000 0000"}
            error={!valid}
            helperText={valid ? "" : "Invalid card number."}
            value={cardNum}
            InputProps={{
              endAdornment: cardType !== "Unknown" ? <img src= {cardIcon} alt={cardType} width="35" /> : ""
            }}
            type="tel"
            autoFocus
          />
        </form>
      </div>
    );
  };

  return (
    <div className="App">
      <CreditCardInput />
    </div>
  );
}

export default App;
